body {
  min-height: 100vh;
  background: #2f363e;
}

.inputs {
  justify-content: center;
  gap: 50px;
  margin-top: 100px;
  display: flex;
}

.inputs input {
  height: 45px;
  width: 150px;
  color: rgba(255, 255, 255, .4);
  letter-spacing: 2px;
  text-align: center;
  background: #262c37;
  border: 0;
  border-bottom: 2px solid gray;
  margin-left: 20px;
  font-size: 16px;
}

.inputs input:hover {
  color: #fff;
  border-bottom: 2px solid #2894ff;
}

label {
  color: #fff;
}

.buttons {
  justify-content: center;
  align-items: center;
  margin-top: 75px;
  display: flex;
}

